import React from "react";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import CircularProgress from "@material-ui/core/CircularProgress";
import { ValidatorForm, TextValidator } from "react-material-ui-form-validator";

function sleep(delay = 0) {
  return new Promise((resolve) => {
    setTimeout(resolve, delay);
  });
}

export default function Asynchronous(props) {
  const [open, setOpen] = React.useState(false);
  const [options, setOptions] = React.useState([]);

  //kiu return tr v ca hm search. Mc nh l return kiu page. ('list' - tr v 1 list)
  const [typeReturnFunction, setTypeReturnFunction] = React.useState("");
  const [displayLable, setDisplayLable] = React.useState(""); // tn trng  hin th ra cho ngi dng chn (mc nh s l code)
  const [defaultValue, setDefaultValue] = React.useState([]); //gi tr mc nh ban u
  const [className, setClassName] = React.useState([]); //class ca th Autocomplete
  const [variant, setVariant] = React.useState([]); //kiu hin th ca th TextValidator
  const [multiple, setMultiple] = React.useState(false); //chn nhiu hay chn 1
  const [validators, setValidators] = React.useState([]); // Truyn vo kiu validators mun check
  const [errorMessages, setErrorMessages] = React.useState([]); //Text hin th khi check validators (theo th t cc validators truyn vo)
  const loading = open && options.length === 0;
  //tm thi khng s dng selectChildName.
  const [selectChildName, setSelectChildName] = React.useState(""); //Nu mun so snh id object bn trong ca value th truyn tn object bn trong Ex: selectChildName = {'objectName'}
  React.useEffect(() => {
    let active = true;

    if (!loading) {
      return undefined;
    }
    (async () => {
      //searchFunction: hm search data
      //searchObject: object cha cc thuc tnh search bn trong
      const response = await props.searchFunction(props.searchObject);
      const data = await (props.typeReturnFunction &&
      props.typeReturnFunction === "list"
        ? response.data
        : response.data.content);
      if (active) {
        setOptions(data);
      }
    })();

    return () => {
      active = false;
    };
  }, [loading]);

  React.useEffect(() => {
    if (!open) {
      setOptions([]);
      setTypeReturnFunction([]);
      setDisplayLable([]);
      setDefaultValue([]);
      setMultiple([]);
      setVariant([]);
      setSelectChildName([]);
      setClassName([]);
      setValidators([]);
      setErrorMessages([]);
    }
  }, [open]);
  return (
    <Autocomplete
      id="asynchronous-demo"
      className={props.className ? props.className : "w-100"}
      open={open}
      autoComplete
      autoHighlight={true}
      multiple={props.multiple}
      onChange={(event, value, reason, details) => {
        let list = [];
        if (props.selectChildName) {
          value.forEach((item) => {
            if (item.id === details.option.id) {
              list.push({ [props.selectChildName]: item });
            } else {
              list.push(item);
            }
          });
        } else {
          list = value;
        }
        props.onSelect(list, props.onSelectOptions);
      }}
      onOpen={() => {
        setOpen(true);
      }}
      onClose={() => {
        setOpen(false);
      }}
      inputValue={props.inputValue}
      getOptionSelected={(option, value) =>
        props.selectChildName && option[props.selectChildName]
          ? option[props.selectChildName].id === value[props.selectChildName].id
          : option.id === value.id
      }
      getOptionLabel={(option) =>
        option
          ? props.selectChildName && option[props.selectChildName]
            ? props.displayLable
              ? option[props.selectChildName][props.displayLable]
              : option[props.selectChildName].code
            : props.displayLable
            ? option[props.displayLable]
            : option.code
          : ""
      }
      options={options}
      loading={loading}
      defaultValue={props.defaultValue ? props.defaultValue : null}
      renderInput={(params) => (
        <TextValidator
          {...params}
          label={props.label}
          value={props.value ? props.value : null}
          validators={props.validators ? props.validators : []}
          errorMessages={props.errorMessages ? props.errorMessages : ""}
          variant={props.variant ? props.variant : "standard"}
          InputProps={{
            ...params.InputProps,
            endAdornment: (
              <React.Fragment>
                {loading ? (
                  <CircularProgress color="inherit" size={10} />
                ) : null}
                {params.InputProps.endAdornment}
              </React.Fragment>
            ),
          }}
        />
      )}
    />
  );
}
